import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import ItemForm from './ItemForm';
import { Container, Row, Col } from 'reactstrap';

function EditBill({bills, setBills}) {
    const {billID} = useParams();
    const [bill, setBill] = useState({});
    const [isBillValid, setIsBillValid] = useState(false);
    useEffect(()=>{
        if(bills[billID]){
            setIsBillValid(true);
            // console.log(bills[billID]);
        }else{
            console.table({"Invalid Bill": bills[billID]})
        }
    }, [bills, billID]);
    return (
        <div className="pt-2">
            <Container>
                {/* <Row>
                    <Col xs={12}><h5>{isBillValid ? bill.name : "Alpha" }</h5></Col>
                </Row> */}
                <Row>
                    <Col>
                        {isBillValid ? <ItemForm bill={bills[billID]} /> : <h3>Loading...</h3>}
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default EditBill
