import React, { useState, useEffect } from 'react'
import { Form, FormGroup, InputGroup, Input, Button, FormText, Row, Col, InputGroupAddon, InputGroupText, Modal, ModalHeader, ModalBody, Container, Alert } from 'reactstrap';
import PairedInput from './PairedInput';

function ItemForm({bill}) {
    // STATES RELATED TO DATA
    const [itemName, setItemName] = useState('');
    const [itemAmount, setItemAmount] = useState('');
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [paidFor, setPaidFor] = useState([]);
    const [paidForRem, setPaidForRem] = useState('')
    const [splitIntoPaidFor, setSplitIntoPaidFor] = useState(0)
    const [paidBy, setPaidBy] = useState([]);
    const [paidByRem, setPaidByRem] = useState('')
    const [splitIntoPaidBy, setSplitIntoPaidBy] = useState(0)

    // STATES RELATED TO UI
    const [itemNameError, setItemNameError] = useState('')
    const [itemAmountError, setItemAmountError] = useState('')
    
    const handleItemNameSubmission = (event) => {
        event.preventDefault();
        const name = itemName.trim();
        if(name===""){ 
            setItemNameError("Cannot be empty");
            return;
        }
        setItemNameError("");

        if(itemAmount===""){
            setItemAmountError("Cannot be empty");
            return;
        }else if(isNaN(itemAmount)){
            setItemAmountError("Must be a number");
            return;
        }
        setItemAmountError("");
        
        const peopleArray = Object.values(bill.details.people);
        
        const paidForNew = peopleArray.map(({id, name}) => {
            return {
                id,
                name,
                value: "",
                splitEqually: false
            }
        })
        const paidByNew = peopleArray.map(({id, name}) => {
            return {
                id,
                name,
                value: "",
                splitEqually: false
            }
        })
        setPaidFor(paidForNew);
        setPaidForRem(Number(itemAmount).toFixed());
        setPaidBy(paidByNew);
        setPaidByRem(Number(itemAmount).toFixed());

        setIsModalOpen(true);
    }

    const updateValue = (i, value, flag) => {
        let people;
        if(flag) people = paidFor.slice();
        else people = paidBy.slice();

        people[i].value = value;
        const totalPaid = people.reduce((acc, person) => acc + Number(person.value), 0)

        if(flag){
            setPaidForRem(Number(itemAmount)-totalPaid);
            setPaidFor(people);
        } 
        else{
            setPaidByRem(Number(itemAmount)-totalPaid);
            setPaidBy(people);
        }
    }

    const split = (people, rem, splitInto) => {
        let value = 0;
        if(splitInto!==0) value = (rem/splitInto).toFixed(2);
        return people.map(person => {
            if(person.splitEqually){
                person.value = value;
            }
            return person;
        })
    }

    

    const toggleSplitEqually = (i, flag) => {
        let people;
        if(flag) people = paidFor.slice();
        else people = paidBy.slice();

        people[i].splitEqually = !people[i].splitEqually;
        people[i].value = "";
        let unequalPaid = 0;
        let splitInto = 0;
        for(const i in people){
            if(people[i].splitEqually) ++splitInto;
            else unequalPaid += Number(people[i].value);
        }
        let value = 0;
        if(splitInto!==0) value = (Number(itemAmount)-unequalPaid)/splitInto;
        value = value.toFixed(2);
        
        let totalPaid=0;
        for(const i in people) {
            if(people[i].splitEqually) people[i].value=value;
            totalPaid += Number(people[i].value);
        }
        const rem = Number(itemAmount)-totalPaid;

        if(flag){
            setPaidFor(people)
            setPaidForRem(rem);
        }else{
            setPaidBy(people)
            setPaidByRem(rem);
        }

    }
    
    return (
        <div>
            <Modal centered isOpen={isModalOpen} toggle={()=>{setIsModalOpen(!isModalOpen)}}>
                <ModalHeader toggle={()=>{setIsModalOpen(!isModalOpen)}}>
                    {/* ITEM DETAILS */}
                    <Row noGutters>
                        <Col className="col-auto p-1 overflow-auto" style={{maxWidth: '75vw'}}>{itemName}</Col>
                        <Col className="col-auto p-1 overflow-auto" style={{maxWidth: '75vw'}}>{Number(itemAmount).toFixed(2)}</Col>
                    </Row>
                </ModalHeader>
                <ModalBody>
                    <Container fluid>
                        {/* PAID FOR */}
                        <Row>
                            <Col style={{textAlign: 'center', color: 'var(--primary)', borderBottom: '2px solid var(--primary)'}} xs={12}>Paid For</Col>
                        </Row>
                        <Row className="my-1" noGutters>
                            {
                                paidFor.map((person, i) => {
                                    return <PairedInput 
                                        flag={true}
                                        splitEqually={person.splitEqually}
                                        toggleSplitEqually={toggleSplitEqually}
                                        name={person.name} 
                                        index={i}
                                        value={person.value}
                                        updateValue={updateValue}
                                        rem={paidForRem}
                                        />
                                })
                            }
                        </Row>

                        {/* PAID BY */}
                        <Row>
                            <Col style={{textAlign: 'center', color: 'var(--primary)', borderBottom: '2px solid var(--primary)'}} xs={12}>Paid By</Col>
                        </Row>
                        <Row  className="my-1" noGutters>
                            {
                                paidBy.map((person, i) => {
                                    return <PairedInput 
                                        flag={false}
                                        splitEqually={person.splitEqually}
                                        toggleSplitEqually={toggleSplitEqually}
                                        name={person.name} 
                                        index={i}
                                        value={person.value}
                                        updateValue={updateValue}
                                        rem={paidByRem}
                                        />
                                })
                            }
                        </Row>

                    </Container>
                </ModalBody>
            </Modal>

            <Form onSubmit={handleItemNameSubmission}>
                <Row>
                <Col>
                    <FormGroup>
                        <InputGroup>
                            <Input type="text" placeholder="Item Name"
                                value={itemName}
                                invalid={itemNameError !== ""}
                                onChange={(e)=>{setItemName(e.target.value)}} />
                            <Input type="number" placeholder="Amount"
                                value={itemAmount}
                                invalid={itemAmountError !== ""}
                                onChange={(e)=>{setItemAmount(e.target.value)}} />
                            <Button color="dark" size="sm" type="submit" >Split</Button>
                        </InputGroup>
                        <Row>
                            <Col xs={5}>
                                <FormText color="danger">{itemNameError}</FormText>
                            </Col>
                            <Col xs={5}>
                                <FormText color="danger">{itemAmountError}</FormText>                            
                            </Col>
                        </Row>
                    </FormGroup>
                </Col>
                </Row>
            </Form>
        </div>
    )
}

export default ItemForm
