import React from 'react'
import { InputGroup, InputGroupAddon, Input, Button, Col } from 'reactstrap';

function PairedInput({flag, splitEqually, toggleSplitEqually, name, index, value, updateValue, rem}) {
    return (
            <Col xs={6} className="p-1" >
                <InputGroup  size={"sm"}>
                    <InputGroupAddon addonType="append">
                        <Button color={splitEqually ? 'primary' : 'secondary'} onClick={()=>{toggleSplitEqually(index, flag)}}>
                            <div style={{overflowX: 'auto', maxWidth: '17vw'}}>{name}</div>
                        </Button>
                    </InputGroupAddon>
                    <Input type="number" placeholder={Number(rem).toFixed(2)}
                        min={0}
                        disabled={splitEqually}
                        value={value}
                        onChange={(e)=>{updateValue(index, e.target.value, flag)}}
                    />
                </InputGroup>
            </Col>        
    )
}

export default PairedInput
